import { Injectable, Optional, Inject, InjectionToken } from '@angular/core';
import { ConsentJourneyConfiguration } from '../models/consent-configuration';

export const ConsentJourneyConfigurationToken = new InjectionToken<ConsentJourneyConfiguration>(
  'ConsentJourneyConfigurationToken injection token',
);

export const consentDefaultConfig: ConsentJourneyConfiguration = {
  multiAccountSelection: false,
};

@Injectable()
export class ConsentJourneyConfigurationService {
  private _config: ConsentJourneyConfiguration;

  constructor(
    @Optional()
    @Inject(ConsentJourneyConfigurationToken)
    private readonly config: ConsentJourneyConfiguration,
  ) {
    this.config = this.config || {};
    this._config = { ...consentDefaultConfig, ...config };
  }

  get multiAccountSelection(): boolean {
    return this._config.multiAccountSelection;
  }
}
