import { Component, OnInit } from '@angular/core';
import { Observable, combineLatest } from 'rxjs';
import { map, pluck } from 'rxjs/operators';
import { ActivatedRoute, ParamMap } from '@angular/router';

export enum Status {
  ALLOW,
  REJECT,
  ERROR,
}

export interface RedirectStatus {
  url: string;
  status: Status;
}

@Component({
  selector: 'bb-tpp-consent-redirect',
  templateUrl: './tpp-consent-redirect.component.html',
})
export class TppConsentRedirectComponent implements OnInit {
  CONSENT_REDIRECT_TIMEOUT = 5000;
  Status = Status;

  readonly allowConsentRequestRedirectUrl: Observable<string> = this.getRouteParam(
    this.route,
    'allowConsentRequestRedirectUrl',
  ).pipe(map(allowConsentRequestRedirectUrl => allowConsentRequestRedirectUrl || ''));

  readonly rejectConsentRequestRedirectUrl: Observable<string> = this.getRouteParam(
    this.route,
    'rejectConsentRequestRedirectUrl',
  ).pipe(map(rejectConsentRequestRedirectUrl => rejectConsentRequestRedirectUrl || ''));

  readonly redirectStatus: Observable<RedirectStatus> = combineLatest(
    this.allowConsentRequestRedirectUrl,
    this.rejectConsentRequestRedirectUrl,
  ).pipe(
    map(([allowUrl, rejectUrl]: [string, string]) => ({
      url: allowUrl ? allowUrl : rejectUrl ? rejectUrl : '',
      status: allowUrl ? Status.ALLOW : rejectUrl ? Status.REJECT : Status.ERROR,
    })),
  );

  private getRouteParam(route: ActivatedRoute, param: string): Observable<string | null> {
    const paramValue = route.paramMap.pipe(pluck<ParamMap, string | null>('params', param));

    if (!route.parent) {
      return paramValue;
    }

    // ensure that level that has actual value keeps it from being cleared
    return combineLatest(paramValue, this.getRouteParam(route.parent, param)).pipe(
      map(params => params[0] || params[1]),
    );
  }

  constructor(private route: ActivatedRoute) {}

  redirect() {
    this.redirectStatus.subscribe((redirectStatus: RedirectStatus) => {
      if (redirectStatus.status !== Status.ERROR) {
        window.location.href = redirectStatus.url;
      }
    });
  }

  ngOnInit() {
    setTimeout(() => {
      this.redirect();
    }, this.CONSENT_REDIRECT_TIMEOUT);
  }
}
