import { Component, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { ConsentJourneyConfigurationService } from '../../../services/consent-journey-config.service';

@Component({
  selector: 'bb-consent-list-view',
  template: ` <div class="bb-card">
    <div class="bb-card__body">
      <bb-consent-list-widget (consentId)="handleDetail($event)"></bb-consent-list-widget>
    </div>
  </div>`,
})
export class ConsentListComponent implements OnDestroy {
  private readonly destroy$ = new Subject<void>();
  consentId = '';

  constructor(
    readonly config: ConsentJourneyConfigurationService,
    private router: Router,
    private activeRoute: ActivatedRoute,
  ) {}

  handleDetail(event: string) {
    this.consentId = event;
    this.router.navigate(['../details', { consentId: event }], {
      relativeTo: this.activeRoute,
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
  }
}
